
* {
	padding: 0;
	margin: 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }

  html,
  body {
	height: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
  }

  ins {
	text-decoration: none;
  }

  img,
  textarea,
  object,
  object embed {
	max-width: 100%;
  }

  img {
	font-size: 0;
	height: auto;
  }

  ul,
  li {
	padding: 0;
	margin: 0;
	list-style: none;
  }

  body {
	position: relative;
	text-align: left;
	line-height: 1.25;
	font-size: 18px;
	color: #000;
	font-family: "Cera Pro", sans-serif;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
	font-family: "Cera Pro", serif;
	font-size: 40px;
	font-weight: 900;
	color: #fff;
	font-style: normal;
  }

  h1 {
	font-size: 70px;
  }

  h2 {
	font-size: 65px;
  }

  h3 {
	font-size: 60px;
  }

  h4 {
	font-size: 50px;
  }

  h5 {
	font-size: 40px;
  }

  h6 {
	font-size: 30px;
  }

  p {
	color: #fff;
	font-weight: 200;
  }

  a {
	text-decoration: none;
	color: #fff;
  }

  a:hover {
	color: #fff;
	text-decoration: none;
	-webkit-filter: brightness(110%);
	filter: brightness(110%);
  }

  select,
  input,
  button {
	border: none;
  }

  select:focus,
  input:focus,
  button:focus {
	outline: none;
  }

  ::-webkit-scrollbar-thumb {
	height: 50px;
	background-color: #ff9000;
	width: 10px;
  }

  ::-webkit-scrollbar-corner,
  ::-webkit-scrollbar-track {
	background-color: #fff;

  }

  ::-webkit-scrollbar {
	width: 10px;
	height: 10px;
  }

  .visible {
	visibility: visible;
  }

  .hide {
	display: none;
  }

  .show {
	display: block;
  }

  .clear {
	visibility: hidden;
	clear: both;
	height: 0;
	font-size: 1px;
	line-height: 0;
  }

  .invisible {
	visibility: hidden;
  }

  .clear-self:after {
	content: '';
	display: block;
	visibility: hidden;
	clear: both;
	height: 0;
	display: table;
  }

  .overflowHidden {
	overflow: hidden;
  }

  .mainWrap {
	position: relative;
	min-height: 100vh;
	overflow: hidden;
	margin: 0 auto;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
  }

  header,
  footer,
  main {
	position: relative;
  }
